<template>
  <div class="mileage">
    <div class="nav">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>当前位置</el-breadcrumb-item>
        <el-breadcrumb-item>车抵贷管理</el-breadcrumb-item>
        <el-breadcrumb-item>里程</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="allWrapper">
      <div class="searchCondition">
        <div>
          <div class="element">
            <p>时间</p>
            <div class="width200">
              <el-date-picker style="width:200px;" v-model="timer" type="daterange" placeholder="选择日期范围">
              </el-date-picker>
            </div>
          </div>
          <div class="element">
            <p>状态</p>
            <div class="width120">
              <el-select v-model="value" placeholder="请选择">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="element">
            <p>平台选择</p>
            <div class="width120">
              <el-select v-model="platform" placeholder="请选择">
                <el-option v-for="item in platforms" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="element">
            <p>手机号</p>
            <div class="width140">
              <el-input type="text" v-model="mobile" placeholder="请输入手机号码" class="input"></el-input>
            </div>
          </div>
          <div class="element">
            <p>账号</p>
            <div class="width140">
              <el-input v-model="zend" placeholder="请输入账号" class="input"></el-input>
            </div>
          </div>
          <div class="element">
            <p>姓名</p>
            <div class="width120">
              <el-input v-model="name" placeholder="请输入姓名" class="input"></el-input>
            </div>
          </div>
          <div class="element" style="margin-top:32px;margin-left:0px;" @click="search">
            <!-- @click="search" -->
            <el-button type="primary">查询</el-button>
          </div>
          <div class="element" style="margin-top:32px;margin-left:0px;" @click="moreBtn">
            <!-- @click="moreBtn" -->
            <el-button type="primary" v-if="!searchCell">更多</el-button>
            <el-button type="primary" v-if="searchCell">收起</el-button>
          </div>
        </div>
        <div style="clear:both;">
          <div class="element" v-show="searchCell">
            <p>身份证号</p>
            <div class="width180">
              <el-input placeholder="请输入身份证号" class="input" v-model="carNum"></el-input>
            </div>
          </div>
          <div class="element" v-show="searchCell">
            <p>所在城市</p>
            <el-input placeholder="请输入所在城市" class="input" v-model="cityOrProvince"></el-input>
          </div>
        </div>
      </div>
      <table>
        <thead>
          <tr>
            <th>时间</th>
            <th>账号</th>
            <th>车型</th>
            <th>上牌时间</th>
            <th>所在城市</th>
            <th>行驶里程</th>
            <th>车辆估价</th>
            <th>姓名</th>
            <th>手机号</th>
            <th>身份证号</th>
            <th>平台选择</th>
            <th>状态</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in getList" :key="item.value" v-show="getList.length > 0">
            <td class="width80">{{item.updated|getFullDate}}</td>
            <td class="width80">{{item.zedAccount}}</td>
            <td class="width200">{{item.title}}</td>
            <td class="width80">{{item.regDate}}</td>
            <td class="width60">{{item.province}}{{item.city}}</td>
            <td class="width60">{{item.mile}}</td>
            <td class="width80">{{item.highPrice}}</td>
            <td class="width60">{{item.name}}</td>
            <td class="width80">{{item.mobile}}</td>
            <td class="width120">{{item.carNum}}</td>
            <td class="width60">{{item.pushPlatformType|getFormtype}}</td>
            <td class="width60">{{item.applyStatus |getStatus}}</td>
          </tr>
          <tr v-show="getList.length === 0">
            <td class="noData" colspan="12">暂无数据</td>
          </tr>
        </tbody>
      </table>
      <div class="block" style="margin-top:20px" v-show="showPageTag">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="pageIndex" :page-size="pageSize" layout="prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { ERR_OK } from '../../common/js/config'
import { format } from '../../common/js/times'
import { cheCredit, testKY } from '../../api/index'
// import { mapMutations } from 'vuex'
export default {
  data() {
    return {
      searchCell: false,
      mobile: '',
      carNum: '',
      name: '',
      cityOrProvince: '',
      zend: '',
      platform: '',
      platforms: [{
        value: '1',
        label: '微贷'
      }],
      options: [{
        value: '1',
        label: '已评估'
      }, {
        value: '3',
        label: '已申请'
      }, {
        value: '5',
        label: '申请成功'
      }],
      provinceCitys: [{
        label: '江苏',
        cities: []
      }, {
        label: '浙江',
        cities: []
      }],
      props: {
        value: 'label',
        children: 'cities'
      },
      pageIndex: 1,
      pageSize: 10,
      total: 1,
      value: '',
      timer: '',
      companyName: '',
      employeeName: '',
      showPageTag: false,
      getList: []
    }
  },
  created() {
    this.getval();
    // testKY();
  },
  filters: {
    getFullDate(t) {
      return format(t)
    },
    getFormtype(t) {
      return t === 1 ? '微贷网' : ''
    },
    getStatus(t) {
      return t === 1 ? '已评估' : t === 3 ? '已申请' : t === 5 ? '申请成功' : ''
    }
  },
  methods: {
    moreBtn() {
      this.searchCell = !this.searchCell
      // console.log(this.searchCell)
    },
    testKY() {
      testKY().then(res => {
        console.log(res)
      })
    },
    // 查看
    look() {
      this.$router.push('/look')
    },
    getval() {
      let params = {
        applyStatus: this.value,
        _startTime: this.timer[0] ? Date.parse(this.timer[0]) : '',
        _endTime: this.timer[1] ? Date.parse(this.timer[1]) : '',
        pushPlatformType: this.platform,
        zedAccount: this.zend,
        cityOrProvince: this.cityOrProvince,
        name: this.name,
        carNum: this.carNum,
        mobile: this.mobile,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      }
      cheCredit(params).then(res => {
        console.log('list len:' + res.count)
        if (res.code === ERR_OK) {
          this.getList = res.list
          this.total = res.count
          if (this.total <= this.pageSize) {
            this.showPageTag = false
          } else {
            this.showPageTag = true
          }
        }
      })
    },
    // 查询
    search() {
      this.getval()
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(val)
      this.pageIndex = val
      this.getval()
    }
  }
}
</script>

<style lang="scss">
@import './Mileage'
</style>




